import React, { useState } from "react";
import { render } from "react-dom";
import FilerobotImageEditor from "filerobot-image-editor";
import "./styles.css";

const App = () => {
  const src = "https://scaleflex.airstore.io/demo/stephen-walker-unsplash.jpg";
  const [show, toggle] = useState(false);
  const [showEditBtn, setShowEditBtn] = React.useState(false);

  const config = {};

  return (
    <div>
      <div className="wrap-image position-relative">
        <img onLoad={() => setShowEditBtn(true)} src={src} alt="example" />
        {showEditBtn && (
          <button
            onClick={() => {
              toggle(true);
            }}
            className="edit-btn btn"
          >
            Edit
          </button>
        )}
      </div>

      <FilerobotImageEditor
        show={show}
        src={src}
        onClose={() => {
          toggle(false);
        }}
      />
    </div>
  );
};

render(<App />, document.getElementById("root"));
